import React, { Component } from 'react';
import './App.css';

import { Route, Link, Switch } from "react-router-dom";
import DataSource from './views/DataSource';
import Dashboard from './views/Dashboard';
import SingleTest from './views/SingleTest';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 'overview'
    }
  }

  componentDidMount() {
  }

  handleClick = (e) => {
    this.setState({
      current: e.key,
    });
  }

  render() {
    return (
      <div className="app">
        <div className="app-nav">
          <div className="app-name">Poli</div>
          <ul className="app-nav-menu">
            <li>
              <Link to="/datasource">
                <i className="fas fa-database fa-fw"></i>
                <span className="menu-text">Data Source</span>
              </Link>
            </li>
            <li>
              <Link to="/dashboard">
                <i className="fas fa-chalkboard fa-fw"></i>
                <span className="menu-text">Dashboard</span>
              </Link>
            </li>
            <li>
              <Link to="/single-test">SingleTest</Link>
            </li>
          </ul>
        </div>
        <div className="app-content">
          <Switch>
            <Route exact path="/" component={Dashboard} />
            <Route exact path="/datasource" component={DataSource} />
            <Route path="/dashboard" component={Dashboard} />
            <Route exact path="/single-test" component={SingleTest} />
          </Switch>
        </div>
      </div>
    );
  }
}
export default App;
